@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
.container {
    width: 90%;
    height: 100vh;
    margin: 0 auto;
    font-family: 'Open Sans', sans-serif;
    /* overflow: hidden; */
    padding-top: 20px;
}

nav {
    position: -webkit-sticky;
    position: sticky;
}

ul {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    border: 4px solid white;
    color: turquoise;
}

li {
    width: 20%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: darkturquoise; */
    color: turquoise;
    text-decoration: none;
    list-style-type: none;
}

li:hover .icon {
    /* background-color: rgb(164, 157, 199); */
    color: rgb(164, 157, 199);
}

h3 {
    font-weight: 700;
    margin: auto;
    padding: 40px 10px 40px 0;
    font-size: 1.8em;
}

p {
    font-size: medium;
}

a,
button {
    font-weight: 700;
    text-align: center;
    margin: auto;
    text-decoration: none;
    color: white;
}

th {
    text-align: left;
    color: darkslategrey;
}

tr th {
    font-weight: 700;
}

td {
    color: slategrey;
}

tr:nth-child(even) {
    background-color: whitesmoke;
}

.id {
    padding-left: 4px;
}

span {
    color: grey;
}

form,
table {
    width: 100%;
    margin: auto;
}


/* buttons */

button {
    padding: 10px 20px;
    border: none !important;
    background-color: transparent;
    color: slategray;
}

.register-area,
.end-form {
    position: fixed;
    width: 100px;
    height: 100px;
    bottom: 100px;
    right: 90px;
    background-color: darkturquoise;
    color: #fff;
    border-radius: 50px;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
    margin: auto;
}

button:hover {
    color: darkslategray;
    border: 1px solid black;
}

.register-area:hover,
.end-form:hover {
    background-color: rgb(164, 157, 199);
    color: white;
}


/* forms */

.input-box {
    width: 100%;
    height: 28px;
    padding: 8px 0;
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: space-between;
}

label {
    vertical-align: bottom;
    padding: 20px 20px;
}

input {
    width: 80%;
    text-decoration: none;
    border: none;
    background-color: whitesmoke;
    padding: 2px 10px;
    color: grey;
    height: 100%;
    font-size: medium;
}

input::-webkit-input-placeholder {
    color: grey;
    font-size: medium;
}

input:-ms-input-placeholder {
    color: grey;
    font-size: medium;
}

input::-ms-input-placeholder {
    color: grey;
    font-size: medium;
}

input::placeholder {
    color: grey;
    font-size: medium;
}

input:focus::-webkit-input-placeholder {
    color: transparent;
    border: none !important;
}

input:focus:-ms-input-placeholder {
    color: transparent;
    border: none !important;
}

input:focus::-ms-input-placeholder {
    color: transparent;
    border: none !important;
}

input:focus::placeholder {
    color: transparent;
    border: none !important;
}

input:focus,
textarea:focus,
select:focus {
    outline: none;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}


/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1.6;
}

ol,
ul {
    /* list-style: none; */
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}
